import React from 'react';
import s from '../Profile.module.css';
import Preloader from '../../common/Preloader/Preloader';
import ProfileStatus from './ProfileStatus';
import userPhoto from '../../../assets/photos/user.png';
import { useDispatch, useSelector } from 'react-redux';
import { savePhoto } from '../../../redux/reducers/profile-reducer';

const ProfileInfo = ({ profile, status, updateUserStatus, isCurrentUserProfile }) => {
    //const status = useSelector((state) => state.profilePage.status);
    const dispatch = useDispatch();

    if (!profile) {
        return <Preloader />
    }

    const onChangeProfilePhoto = (e) => {
        debugger
        const selectedFile = e.target.files[0];
        if (selectedFile) {
            dispatch(savePhoto(selectedFile));
        }
    }

    debugger
    return (
        <div className={s.container}>
            <img src='https://www.topgear.com/sites/default/files/2022/07/13.jpg' alt='' />
            <img className={s.photoLarge} src={profile.photos.large ? profile.photos.large : userPhoto} alt='' />
            {isCurrentUserProfile && <input type='file' onChange={onChangeProfilePhoto} />}
            <ProfileStatus status={status} updateUserStatus={updateUserStatus} />
            <h2>
                {profile.fullName}
            </h2>
            <h3>
                {profile.aboutMe}
            </h3>
        </div>
    );
}

export default ProfileInfo;