import React from 'react';
import ProfileInfo from "./ProfileInfo/ProfileInfo";
import MyPostsContainer from './MyPosts/MyPostsContainer';


const Profile = ({profile, status, updateUserStatus, isCurrentUserProfile}) => {
    return (
        <div>
            <ProfileInfo profile={profile} status={status} updateUserStatus={updateUserStatus} isCurrentUserProfile={isCurrentUserProfile}/>
            <MyPostsContainer />
        </div>
    );
}



export default Profile;